@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Alegreya+Sans&family=Cabin:wght@500&family=IBM+Plex+Sans+Arabic:wght@500&family=Play&display=swap");
* {
  margin: 0;
  padding: 0;
  user-select: none;
  font-family: "Montserrat Arabic";
}

body {
  position: relative;
  background-color: #fafafa;
  width: 100vw;
  overflow-x: hidden !important;
}

#header {
  z-index: 9999;
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 30px 40px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  font-family: Cabin;
}
#header > div.menu {
  text-align: right;
  display: grid;
  grid-auto-flow: column;
  gap: 15px;
  justify-items: end;
  justify-content: end;
}
#header > div.menu > .menu-item {
  text-decoration: none;
  color: white;
}
#header > .logo-container {
  background: url(../assets/logo.png) center no-repeat;
  background-size: cover;
  width: 150px;
  height: 55px;
}

.section {
  min-height: 100vh;
  width: 100vw;
  box-sizing: border-box;
}

#hero {
  background: url(../assets/hero-bg.jpg) no-repeat center fixed;
  background-size: cover;
  position: relative;
}
#hero > .overlay {
  position: absolute;
  inset: 0;
  background: rgb(2, 0, 36);
  background: linear-gradient(180deg, rgba(2, 0, 36, 0) 0%, #0f54ef 100%);
  display: grid;
  place-content: center;
  place-items: center;
}
#hero > .overlay > .title {
  font-size: 3rem;
  color: white;
  font-weight: 900;
  text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.33);
}
#hero > .overlay > .big-title {
  margin-bottom: 15px;
  position: relative;
  line-height: 0.75;
}
#hero > .overlay > .big-title > h1 {
  font-size: 11rem;
  font-family: "Montserrat";
  color: white;
  z-index: 10;
}
#hero > .overlay > .big-title > h1:nth-child(2) {
  margin-left: 150px;
}

@media screen and (max-width: 975px) {
  #hero > .overlay > .more {
    font-size: 0.9rem;
    grid-template-columns: 1fr !important;
  }
  #hero > .overlay > .title {
    font-size: 1.2rem;
  }
  #hero > .overlay > .big-title {
    line-height: 0.75;
  }
  #hero > .overlay > .big-title > h1 {
    font-size: 6rem;
    z-index: 10;
  }
  #hero > .overlay > .big-title > h1:nth-child(2) {
    margin-left: 100px;
  }
}
@media screen and (max-width: 400px) {
  .triangle {
    height: 200px !important;
    width: 200px !important;
  }
  .triangle-shadow {
    height: 200px !important;
    width: 200px !important;
  }
  #guests > .title {
    font-size: 2rem !important;
  }
}
@media screen and (max-width: 500px) {
  #guests-list > .overlay {
    max-height: 300px;
    overflow-y: scroll;
    overflow-x: hidden;
  }
  #hero > .overlay > .more {
    font-size: 0.7rem;
    grid-template-columns: 1fr !important;
  }
  #hero > .overlay > .title {
    font-size: 1rem;
  }
  #hero > .overlay > .big-title {
    line-height: 0.75;
  }
  #hero > .overlay > .big-title > h1 {
    font-size: 3.54rem;
    z-index: 10;
  }
  #hero > .overlay > .big-title > h1:nth-child(2) {
    margin-left: 70px;
  }
}
#hero > .overlay > .more {
  padding: 15px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
}
#hero > .overlay > .more > h4, #hero > .overlay > .more > h3 {
  color: white;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.33);
  text-align: center;
}

.marquee {
  position: absolute;
  bottom: 0;
  width: 100vw;
  white-space: nowrap;
  overflow: hidden;
  box-sizing: border-box;
  padding: 15px;
  font-size: 1.2rem;
  z-index: 10;
}

.marquee p {
  font-family: "Montserrat Arabic" !important;
  display: inline-block;
  color: #fff;
  text-align: center;
  width: 100%;
}

.cta {
  display: grid;
  text-align: center;
  margin: 10px;
  text-decoration: none;
  padding: 7px 22px;
  border: solid 4px #0f54ef;
  border-radius: 17px;
  color: white;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.33);
  font-size: 2rem;
}

#presentation {
  position: relative;
  background-color: #0f54ef;
  text-align: justify;
  display: grid;
  place-content: center;
  gap: 15px;
  font-size: 2rem;
  color: #fafafa;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.33);
  overflow: none;
}
#presentation > h4 {
  margin: auto;
  text-align: center;
  width: 55vw;
  margin-top: 50px;
  margin-bottom: 50px;
  z-index: 10;
}
#presentation > .video {
  z-index: 10;
  border-radius: 20px;
}
#presentation > .video > video {
  width: 100%;
}

#about {
  z-index: 10;
  font-size: 1.5rem;
  width: 40vw;
  max-width: 1366px;
  min-width: 250px;
  margin: auto;
  margin-bottom: 70px;
  margin-top: 50px;
  padding: 25px 45px;
  /* From https://css.glass */
  background: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

#timeline {
  background-color: #eaf6ff;
  text-align: center;
}
#timeline > img {
  height: 120px;
  margin: auto;
}
#timeline > h1 {
  color: #0f54ef;
  font-size: 3rem;
  text-align: center;
  padding: 15px;
  padding-top: 70px;
}
@media screen and (max-height: 400px) {
  #timeline > h1 {
    font-size: 1.8rem;
    padding-top: 20px;
  }
}

#guests {
  background: #0f54ef;
  padding: 50px;
}
#guests > h1.title {
  position: relative;
  z-index: 10;
  font-family: "Montserrat Arabic";
  text-align: center;
  padding: 50px;
  color: white;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.33);
  font-size: 4rem;
}

#guests-list {
  direction: rtl;
  max-width: 1366px;
  min-width: 250px;
  width: 70vw;
  margin: auto;
  text-align: center;
  background: url(../assets/hero-bg.jpg) no-repeat center fixed rgba(255, 255, 255, 0.2);
  background-size: cover;
  border-radius: 16px;
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  overflow: hidden;
}
#guests-list > .overlay {
  padding: 50px 25px;
  background: rgb(2, 0, 36);
  background: linear-gradient(180deg, rgba(2, 0, 36, 0) 0%, #0f54ef 100%) fixed;
  max-height: 500px;
  overflow-y: scroll;
  overflow-x: hidden;
}
#guests-list > .overlay > .guest {
  margin-top: 7px;
  margin-right: 7px;
  display: inline-block;
  padding: 7px 15px;
}
#guests-list > .overlay > .guest > .name {
  font-weight: bold;
  color: white;
  font-size: 1.3rem;
}
#guests-list > .overlay > .guest > .name::after {
  content: "•";
  font-size: 1.5rem;
}
#guests-list > .overlay > .guest > .title {
  font-weight: bold;
  color: #ccc;
  font-size: 0.8rem;
}

.separation {
  position: relative;
}

.accent {
  background-color: white;
  color: #0f54ef;
  font-size: 2.3rem;
}

.triangle {
  content: " ";
  height: 400px;
  width: 400px;
  clip-path: polygon(50% 0, 0% 100%, 100% 100%);
  background-color: rgba(15, 84, 239, 0.2);
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.33);
  z-index: 0;
}

.triangle-shadow {
  content: " ";
  height: 400px;
  width: 400px;
  clip-path: polygon(50% 0, 0% 100%, 100% 100%);
  background-color: rgba(0, 0, 0, 0.33);
}

.shape-bg-1-shadow {
  bottom: -14% !important;
}

.shape-bg-2-shadow {
  top: -14% !important;
}

.shape-bg-1 {
  position: absolute;
  left: 0;
  bottom: -15%;
  transform: rotate(90deg);
}

.shape-bg-2 {
  position: absolute;
  right: 0;
  top: -15%;
  transform: rotate(-90deg);
}

/* width */
::-webkit-scrollbar {
  width: 3px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #0f54ef;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #fff;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(15, 84, 239, 0.2);
}

.invitation {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.invitation-guest {
  color: wheat;
  text-align: center;
  font-size: 1.8rem;
  margin-bottom: 15px;
}

.invitation-message {
  color: white;
  text-align: center;
  font-size: 1.2rem;
}

#companies {
  background-color: black;
  display: grid;
  grid-auto-flow: column;
  place-content: center;
  gap: 25px;
  padding: 25px;
}
#companies > img {
  height: 100px;
}

#footer {
  background-color: black;
  display: grid;
  grid-auto-flow: column;
  place-content: center;
  gap: 25px;
  padding: 25px;
}
#footer > a {
  display: grid;
  width: 30px;
  height: 30px;
}
#footer > a > img {
  width: 100%;
  height: 100%;
}

.download {
  width: fit-content;
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  place-content: center;
  place-items: center;
}
@media screen and (max-width: 992px) {
  .download {
    grid-template-columns: 1fr;
  }
}
.download a img {
  width: 250px;
}
.download a.website {
  text-decoration: none;
  color: white;
}
.download a.website.dark {
  color: #0f54ef;
}

#copyrights {
  color: white;
  margin: auto;
}